<template>
  <div>
    <el-form ref="passFormRef" v-if="true" :rules="passRules" :model="passForm" label-position="left" label-width="0px">
      <div style="text-align: left;"></div>
      <el-form-item style="width: 100%" prop="oldpassword">
        <el-input :clearable="true" :show-password="true" :validate-event="true" type="password" v-model="passForm.oldpassword" auto-complete="off" placeholder=""/>
      </el-form-item>
      <div style="text-align: left;">8~18</div>
      <el-form-item style="width: 100%" prop="newpassword">
        <el-input :clearable="true" :show-password="true" :validate-event="true" type="password" v-model="passForm.newpassword" auto-complete="off" placeholder=""/>
      </el-form-item>
      <div style="text-align: left;"></div>
      <el-form-item style="width: 100%" prop="repassword">
        <el-input :clearable="true" :show-password="true" :validate-event="true" type="password" v-model="passForm.repassword" auto-complete="off" placeholder=""/>
      </el-form-item>
      <el-button type="primary" @click.native.prevent="onsubmit"></el-button>
    </el-form>
  </div>
</template>

<script>
  import {isvalidPass} from '../Login'

  export default {
    name: 'PersonPassword',
    data (){
      //
      let validatePass = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else if (!isvalidPass(value)) {
          callback(new Error(" 8~18 "));
        }
        callback();

      };

      //
      let validatePass1 = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else if (!isvalidPass(value)) {
          callback(new Error(" 8~18 "));
        } else {
          if (this.passForm.repassword !== "") {
            this.$refs.passFormRef.validateField("repassword");
          }
          callback();
        }
      };

      //
      let validatePass2 = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else if (value !== this.passForm.newpassword) {
          callback(new Error(""));
        } else {
          callback();
        }
      };

      return{
        passForm: {
          oldpassword: '',
          newpassword: '',
          repassword: ''
        },
        //
        passRules: {
          oldpassword: [
            { required: true, validator: validatePass, trigger: "blur" }
          ],
          newpassword: [
            { required: true, validator: validatePass1, trigger: "blur" }
          ],
          repassword: [
            { required: true, validator: validatePass2, trigger: "blur" }
          ]
        },
      }
    },

    methods: {
      onsubmit() {
        this.$refs.passFormRef.validate((valid) => {
          if (valid) {
            this.$axios.post('/passChange', {
              user_id: sessionStorage.getItem("user_id"),
              oldpassword: this.passForm.oldpassword,
              newpassword: this.passForm.newpassword
            })
              .then(res => {
                if (res.status === 200 && res.data && res.data.code === 200) {
                  this.$message({
                    message: '!',
                    type: 'success',
                    center: true
                  });
                  this.$router.replace({path: '/login'})
                } else if(res.status === 200 && res.data && res.data.code === 201) {
                  this.$message({
                    message: '',
                    type: 'error',
                    center: true
                  });
                } else if(res.status === 200 && res.data && res.data.code === 202) {
                  this.$message({
                    message: '',
                    type: 'error',
                    center: true
                  });
                }
              })
              .catch(err => {
                console.log(err);
              });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

<style scoped>

</style>



