<template>
  <div>
    <div style="text-align: left; margin: 5px">
      <el-input clearable prefix-icon="iconfont icon-sousuo" v-model="searchTableInfo" placeholder="" style="width:240px"></el-input>
    </div>

    <!---->
    <el-table :height="tableHeight"
              ref="yonghuTable"
              border
              :data="yonghuData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
              tooltip-effect="dark"
              style="width: auto"
              :row-style="{height:'25px',width:'auto'}"
    >
      <!---->
      <el-table-column type="selection" width="auto"></el-table-column>
      <el-table-column sortable align="center" prop="user_name" label="" width="auto">
        <template slot-scope="{row,$index}">
          <span>{{row.user_name}}</span>
        </template>
      </el-table-column>
      <el-table-column sortable align="center" prop="user_level" label="" width="auto">
        <template slot-scope="{row,$index}">
          <el-tag type="info" v-if="row.user_level === 0"></el-tag>
          <el-tag v-else-if="row.user_level === 1"></el-tag>
        </template>
      </el-table-column>
      <el-table-column sortable align="center" prop="login_username" label="" width="auto">
        <template slot-scope="{row,$index}">
          <span>{{row.login_username}}</span>
        </template>
      </el-table-column>
      <el-table-column sortable align="center" prop="user_state" label="" width="auto">
        <template slot-scope="{row,$index}">
          <el-select v-if="showSelect[$index]"
                     style="width: 100%"
                     v-model="row.user_state"
                     filterable
          >
            <el-option
              id="user_stateSelect"
              v-for="item in user_stateOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span v-if="!showSelect[$index]">
              <el-tag v-if="row.user_state === 0"></el-tag>
              <el-tag type="info" v-else-if="row.user_state === 1"></el-tag>
              <el-tag type="danger" v-else-if="row.user_state === 2"></el-tag>
          </span>
        </template>
      </el-table-column>
      <!---->
      <el-table-column align="center" label="" width="400px" fixed="right">
        <template slot-scope="{row,$index}">
          <el-button size="mini" @click.native="handleUpdate(row)" v-if="showBtn[$index]">
            
          </el-button>
          <el-button size="mini" @click.native="handleCancle($index)" v-if="showBtn[$index]">
            
          </el-button>

          <el-button size="mini" @click.native="handleEdit($index)" v-if="!showBtn[$index]">
            
          </el-button>

          <el-button size="mini" @click="getAllLoginip(row)">
            
          </el-button>
          <!---->
          <el-popover placement="top" >
            <h5 align="center"></h5>
            <div style="text-align: center; margin: 0">
              <el-button type="danger" size="mini" @click="deleteRow(row)"> </el-button>
            </div>
            <el-button size="mini" slot="reference"> </el-button>
          </el-popover>

          <!---->
          <el-popover placement="top" >
            <h5 align="center">jidegaimima1234</h5>
            <div style="text-align: center; margin: 0">
              <el-button type="danger" size="mini" @click="replaceRow(row)"> </el-button>
            </div>
            <el-button size="mini" slot="reference"></el-button>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>


    <el-dialog title="" :visible.sync="dialogVisible">
      <el-table :height="tableHeight"
                ref="loginipTable"
                border
                :data="allLoginipData"
                tooltip-effect="dark"
                style="width: auto"
                :row-style="{height:'25px',width:'auto'}"
      >
        <el-table-column align="center" prop="ip_date" label="" width="auto">
        </el-table-column>
        <el-table-column align="center" prop="ip_city" label="" width="auto">
        </el-table-column>
        <el-table-column align="center" prop="ip_ip" label="IP" width="auto">
        </el-table-column>
        <el-table-column align="center" prop="ip_device" label="" width="auto">
        </el-table-column>
        <el-table-column align="center" prop="ip_OS" label="" width="auto">
        </el-table-column>
      </el-table>
    </el-dialog>

    <!---->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="yonghuData.length">
    </el-pagination>
  </div>
</template>

<script>
  import Clipboard from 'clipboard'

  var qs = require('qs');
  export default {
    data () {
      //
      let validInsert = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else {
          callback();
        }
      };

      return {
        searchTableInfo:"",
        currentPage: 1,
        pageSize: 15,
        tableHeight: window.innerHeight * 0.85,
        yonghuData: [],
        showEdit: [], //
        showBtn: [],
        showSelect: [],
        allLoginipData: [],
        dialogVisible: false,
        user_stateOptions: [
          {
            value: '0',
            label: ''
          },
          {
            value: '1',
            label: ''
          }
        ]
      }
    },

    mounted () {
      this.getAllYonghu();
    },

    watch: {
      //
      searchTableInfo(){
        var searchTableInfo = this.searchTableInfo;
        if(searchTableInfo){
          this.yonghuData = this.yonghuData.filter(value => value.user_name.indexOf(this.searchTableInfo) !== -1)
        } else {
          this.getAllYonghu();
        }
      },
    },

    methods: {
      //
      cancelSelection() {
        this.$refs.yonghuTable.clearSelection();
      },

      //
      getAllYonghu() {
        var getData = this;
        this.$axios.get('users/getYonghu')
          .then((res) => {
            getData.yonghuData = res.data.result;
          })
          .catch((err) =>{
            console.log(err);
            alert('');
          })
      },

      //
      //
      handleEdit(index) {
        this.$set(this.showEdit,index,true)
        this.$set(this.showSelect,index,true)
        this.$set(this.showBtn,index,true)
      },
      //
      handleCancle(index) {
        this.$set(this.showEdit,index,false)
        this.$set(this.showSelect,index,false)
        this.$set(this.showBtn,index,false)
      },
      //
      handleUpdate(row) {
        var params = qs.stringify({
          user_id: row.user_id,
          user_state: row.user_state
        })
        this.$axios.post('/users/updateUserState', params)
          .then(res => {
            if (res.status === 200 && res.data && res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success',
              });
              this.$router.go(0);
            } else if (res.status === 200 && res.data && res.data.code === 201) {
              this.$notify({
                title: '',
                type: 'error'
              });
            }
          });
      },

      //
      deleteRow(row) {
        var params = qs.stringify({
          user_id: row.user_id
        })
        this.$axios.post('/users/deleteUser', params)
          .then(res => {
            if (res.status === 200 && res.data && res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success',
              });
              this.$router.go(0);
            } else if (res.status === 200 && res.data && res.data.code === 201) {
              this.$notify({
                title: '',
                type: 'error'
              });
            }
          });
      },

      //
      replaceRow(row) {
        var params = qs.stringify({
          user_id: row.user_id
        })
        this.$axios.post('/passReplace', params)
          .then(res => {
            if (res.status === 200 && res.data && res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success',
              });
              this.$router.go(0);
            } else if (res.status === 200 && res.data && res.data.code === 201) {
              this.$notify({
                title: '',
                type: 'error'
              });
            }
          });
      },

      //
      getAllLoginip (row) {
        this.dialogVisible = true
        var getData = this;
        this.$axios.get('/loginip/getAllByUserId', {
          params: {
            user_id: row.user_id
          }
        })
          .then((res) => {
            getData.allLoginipData = res.data.result;
          })
          .catch((err) => {
            console.log(err);
            alert('');
          })
      },

      handleSizeChange: function (size) {
        this.pagesize = size
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage
      }
    }
  }
</script>

<style scoped>

</style>



