// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {Loading} from 'element-ui'

import store from './assets/store'
import * as IpUtils from './assets/iputils'

Vue.use(ElementUI);
Vue.use(Vuex);
Vue.use(qs);
Vue.prototype.$IpUtils = IpUtils;
Vue.prototype.$axios = axios;

// cookie
axios.defaults.withCredentials = true

Vue.config.productionTip = false
//  http://localhost:8888/api
//Prod
axios.defaults.baseURL = '/api'
//Dev
//axios.defaults.baseURL = 'http://localhost:8888/api'

/* eslint-disable no-new */

//loading
let loading;
//
let loadingNum=0;
function startLoading() {
  if(loadingNum==0){
    loading = Loading.service({
      lock: true,
      text: '...',
      background:'rgba(255,255,255,0.5)',
      time: 1500
    })
  }
  //1
  loadingNum++;
}
function endLoading() {
  //1
  loadingNum--
  if(loadingNum<=0){
    loading.close()
  }
}
//
axios.interceptors.request.use(request => {
  startLoading();
  return request
}, err => {
  return Promise.reject(err);
});
//
axios.interceptors.response.use(response => {
  endLoading();
  return response
}, err => {
  endLoading();
  if (err && err.response) {
    switch (err.response.status) {
      case 400: err.message = '(400)'; break;
      case 401: this.$router.push('/login'); break;
      case 403: err.message = '(403)'; break;
      case 404: err.message = '(404)'; break;
      case 408: err.message = '(408)'; break;
      case 500: err.message = '(500)'; break;
      case 501: err.message = '(501)'; break;
      case 502: err.message = '(502)'; break;
      case 503: err.message = '(503)'; break;
      case 504: err.message = '(504)'; break;
      case 505: err.message = 'HTTP(505)'; break;
      default: err.message = `(${err.response.status})!`;
    }
  } else {
    err.message = '!'
  }
  message.error(err.message);
  return Promise.reject(err);
});

//
router.beforeEach((to,from,next)=>{
  if (to.meta.isLogin == true) {//
    if (sessionStorage.getItem("msg") != null) {
      next();
    } else {
      alert("")
      next({
        path:"/login",
        query:{redirect:to.fullPath}
      });
    }
  } else {
    next();
  }

  // 
  if (to.meta.level == 2) {
    if (sessionStorage.getItem("user_level") == 2) {
      next();
    } else {
      alert("");
      next(from);
    }
  } else {
    next();
  }
})

new Vue({
  store,
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



