<template>
  <el-upload
    class="upload-demo"
    ref="upload"
    action="http://localhost:8888/api/netdisk/upload"
    :on-preview="handlePreview"
    :before-upload="beforeUpload"
    :data="uploadData"
    :on-error = "error"
    :on-success="successResave"
    :on-remove="handleRemove"
    :file-list="fileList"
    :auto-upload="false">
    <el-button slot="trigger" size="small" type="primary"></el-button>
    <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
    <div slot="tip" class="el-upload__tip">1GB2GB</div>
  </el-upload>
</template>

<script>
  var qs = require('qs');
  export default {
    name: 'NetDiskUpload',
    data() {
      return {
        fileList: [],
        uploadData: null
      };
    },

    methods: {
      //
      submitUpload() {
        this.$refs.upload.submit();
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      beforeUpload (file) {
        this.uploadData = {
          user_id: sessionStorage.getItem("user_id"),
          netdisk_uploaduser: sessionStorage.getItem("login_username")
        };
        let promise = new Promise((resolve) => {
          this.$nextTick(function () {
            resolve(true);
          });
        });
        return promise; //promis
      },
      //
      successResave(res, file, fileList){
        if(res.status === 200 && res.data && res.data.code === 201){
          alert("")
          this.proType="";
          this.mobanname="";
          this.value5="";
        }
      },
      //
      error(response, file, fileList){
        console.log(response)
      },
      // submitUpload() {
      //   this.$refs.upload.submit();
      // },
      // handleRemove(file, fileList) {
      //   console.log(file, fileList);
      // },
      // handlePreview(file) {
      //   console.log(file);
      // },
      // handleExceed (files, fileList) {
      //   this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `)
      // },
      // beforeRemove (file, fileList) {
      //   return this.$confirm(` ${file.name}`)
      // },
      // handleSuccess (response) {
      //   this.url = response
      //   this.$emit('onUpload')
      //   this.$message.warning('')
      // },
      // clear () {
      //   this.$refs.upload.clearFiles()
      // }
    }
  }
</script>

