<template>
  <div>
    <div style="text-align: left; margin: 5px">
      <el-input clearable prefix-icon="el-icon-search" v-model="searchTableInfo" placeholder="" style="width:240px"></el-input>
    </div>
    <!--    -->
    <el-table ref="lutiTable"
              class="lutiTable"
              :height="tableHeight"
              border
              :data="MyLuruDataList.slice((currentPage-1)*pageSize, currentPage * pageSize)"
              tooltip-effect="dark"
              :row-style="{height:'25px',width:'auto'}"
              style="width: auto"
    >
      <!---->
      <el-table-column type="selection" width="auto"></el-table-column>
      <el-table-column align="center" prop="luti_code" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="xuexiao_mingcheng" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="kemu_mingcheng" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="luti_shuoming" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="luti_jiezhishijian" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="lutistate_lurubeizhu" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="lutistate_lurustate" label="" width="auto">
        <template slot-scope="{row,$index}">
          <el-tag type="warning" v-if="row.lutistate_lurustate === 0"></el-tag>
          <el-tag v-else-if="row.lutistate_lurustate === 1"></el-tag>
        </template>
      </el-table-column>
<!--      <el-table-column align="center" prop="lutistate_shenhestate" label="" width="auto">-->
<!--        <template slot-scope="{row,$index}">-->
<!--          <span>-->
<!--            <el-tag type="info" v-if="row.lutistate_shenhestate === 0"></el-tag>-->
<!--            <el-tag type="warning" v-else-if="row.lutistate_shenhestate === 1"></el-tag>-->
<!--            <el-tag v-else-if="row.lutistate_shenhestate === 2"></el-tag>-->
<!--          </span>-->
<!--        </template>-->
<!--      </el-table-column>-->
      <el-table-column align="center" prop="lutistate_state" label="" width="auto">
        <template slot-scope="{row,$index}">
          <el-tag type="warning" v-if="row.lutistate_state === 0"></el-tag>
          <el-tag type="warning" v-else-if="row.lutistate_state === 1"></el-tag>
          <el-tag v-else-if="row.lutistate_state === 2"></el-tag>
          <el-tag type="danger" v-else-if="row.lutistate_state === 3"></el-tag>
        </template>
      </el-table-column>
      <!---->
      <el-table-column align="center" label="" width="240px" fixed="right">
        <template slot-scope="{row,$index}">
          <!---->
          <el-popover placement="top" >
            <h5 align="center"></h5>
            <div style="text-align: center; margin: 0">
              <el-button type="danger" size="mini" @click="successRow(row)"> </el-button>
            </div>
            <el-button size="mini" slot="reference"></el-button>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>

    <!--    -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="MyLuruDataList.length">
    </el-pagination>
  </div>
</template>

<script>
  var qs = require('qs');
  export default {
    name: 'MyLuruTask',
    data() {
      return {
        tableHeight: window.innerHeight * 0.85,
        MyLuruDataList: [],
        searchTableInfo:"",
        currentPage: 1,
        pageSize: 15,
      }
    },

    watch: {
      //
      searchTableInfo(){
        var searchTableInfo = this.searchTableInfo;
        if(searchTableInfo){
          this.MyLuruDataList = this.MyLuruDataList.filter(value => value.luti_code.indexOf(this.searchTableInfo) !== -1)
        } else {
          this.getAllLutiByLururenId()
        }
      },
    },

    mounted () {
      this.getAllLutiByLururenId()
    },

    methods: {
      //
      getAllLutiByLururenId() {
        var getData = this;
        this.$axios.get('/luti/getAllLutiByLururenId', {
          params:{
            lutistate_lururenid: sessionStorage.getItem("user_id")
          }
        })
          .then((res) => {
            getData.MyLuruDataList = res.data.result;
          })
          .catch((err) =>{
            console.log(err);
            alert('');
          })

      },

      //
      successRow(row) {
        var params = qs.stringify({
          luti_id: row.luti_id,
          lutistate_lururenid: sessionStorage.getItem("user_id")
        })
        console.log(row.luti_id)
        this.$axios.post('luti/accLuruTask', params)
          .then(res => {
            if (res.status === 200 && res.data && res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success',
              });
              this.$router.go(0);
            } else if (res.status === 200 && res.data && res.data.code === 201) {
              this.$notify({
                title: '',
                type: 'error'
              });
            }
          });
      },

      handleSizeChange: function (val) {
        this.pagesize = val
      },
      handleCurrentChange: function (val) {
        this.currentPage = val
      }
    }
  }
</script>

<style scoped>

</style>



