import Vue from 'vue';
import Vuex from 'vuex';
//  router vuexrouter
import router from '../router/index';

Vue.use(Vuex);
Vue.use(router);

export default new Vuex.Store({
  state: {
    //Tabs
    tabsPage: [],
    TabsValue: ''
  },
  mutations: {
    //Tabs
    editableTabs: (state, obj) => {
      //  state.tabsPage
      let arr = Array.from(state.tabsPage);
      // 
      if (arr.length !== 0) {
        //  Array.some 
        var even = function(obj) {
          return arr.some(item => {
            return item.name === obj.routeName;
          });
        };
        // even true,flase
        //  true 
        if (!even(obj)/* pushbing */) {
          // tabspusharr
          arr.push({ title: obj.name, name: obj.routeName });
          // tabsPage
          state.tabsPage = arr;
          // TabsValue
          state.TabsValue = obj.routeName;
          // 
          router.push({ name: obj.routeName });
        } else { //  
          // TabsValue
          state.TabsValue = obj.routeName;
          // 
          router.push({ name: obj.routeName });
        };
      } else {// 0
        // tabspusharr
        arr.push({
          title: obj.name, name: obj.routeName
        });
        // tabsPage
        state.tabsPage = arr;
        // TabsValue
        state.TabsValue = obj.routeName;
        // 
        router.push({ name: obj.routeName });
      }
    }
  },
  actions: {
    // 
    editableTabs (context, obj) {
      context.commit('editableTabs', obj);
    }
  }
});



