<template>
  <div>
    <div style="text-align:left" class="headtext">
      <p style="font-size: 25px"></p> &nbsp;&nbsp;&nbsp;
      <p style="color: #C0C4CC; font-size: 10px"></p> &nbsp;
      <p style="color: red; font-size: 10px"> ()</p>
    </div>
    <!---->
    <el-table :height="tableHeight"
              ref="loginipTable"
              border
              :data="loginipData"
              tooltip-effect="dark"
              style="width: auto"
              :row-style="{height:'25px',width:'auto'}"
    >
      <el-table-column align="center" prop="ip_date" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="ip_city" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="ip_ip" label="IP" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="ip_device" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" prop="ip_OS" label="" width="auto">
      </el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-button class="copy-btn" @click="copy(scope.row)">
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import Clipboard from 'clipboard';
  var qs = require('qs');
  export default {
    name: "PersonIP",

    data () {
      return {
        tableHeight: window.innerHeight * 0.85,
        loginipData: []
      }
    },

    mounted () {
      this.getLoginip();
    },

    methods: {
      //
      getLoginip () {
        var getData = this;
        this.$axios.get('/loginip/getAllByUserId', {
          params: {
            user_id: sessionStorage.getItem("user_id")
          }
        })
          .then((res) => {
            getData.loginipData = res.data.result;
          })
          .catch((err) => {
            console.log(err);
            alert('');
          })
      },

      // 
      copy (row) {
        console.log(row.ip_date)
        let copyText = ":" + row.ip_date.toString() +
                       " :" + row.ip_city.toString() +
                       " IP:" + row.ip_ip.toString() +
                       " :" + row.ip_device.toString() +
                       " :" + row.ip_OS.toString();
        var clipboard = new Clipboard('.copy-btn', {
          text: function() {
            return copyText;
          }
        });
        clipboard.on('success', e => {
          this.$message({
            message: '!',
            type: 'success'
          });
          // 
          clipboard.destroy()
        })
        clipboard.on('error', e => {
          // 
          this.$message({
            message: '!',
            type: 'error'
          });
          // 
          clipboard.destroy()
        })
      }
    }
  }
</script>

<style scoped>
.headtext p{
  display:inline-table
}
</style>



