<template>
  <el-row :gutter="20">
    <el-col :span="8"><div class="grid-content bg-purple">
      <!---->
      <el-form ref="loginFormRef" v-if="true" class="login-container" label-position="left" label-width="0px" :rules="loginRules" :model="loginForm">
        <h1 class="login_title">TAT</h1>
        <el-form-item prop="username">
          <el-input :clearable="true" :validate-event="true" type="text" v-model="loginForm.username" auto-complete="off" placeholder=""/>
        </el-form-item>
        <br>
        <el-form-item prop="password">
          <el-input :clearable="true" :show-password="true" :validate-event="true" type="password" v-model="loginForm.password" auto-complete="off" placeholder=""/>
        </el-form-item>
        <br><br><br>
        <el-form-item style="width: 100%" >
          <el-button round type="primary" style="width: 100%;border: none" @click="login"></el-button>
        </el-form-item>
        <el-form-item style="width: 100%">
          <el-button round style="width: 100%;" @click="dialogFormVisible = true"></el-button>
        </el-form-item>

        <!---->
        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form ref="registerFormRef" v-if="true" :rules="registerRules" :model="registerForm" label-position="left" label-width="0px">
            <div style="text-align: left;"></div>
            <el-form-item style="width: 100%" prop="user">
              <el-input :clearable="true" :validate-event="true" type="text" v-model="registerForm.user" auto-complete="off" placeholder=""/>
            </el-form-item>
            <div style="text-align: left;">4-15</div>
            <el-form-item style="width: 100%" prop="username">
              <el-input :clearable="true" :validate-event="true" type="text" v-model="registerForm.username" auto-complete="off" placeholder=""/>
            </el-form-item>
            <div style="text-align: left;">8~18</div>
            <el-form-item style="width: 100%" prop="password">
              <el-input :clearable="true" :show-password="true" :validate-event="true" type="password" v-model="registerForm.password" auto-complete="off" placeholder=""/>
            </el-form-item>
            <div style="text-align: left;"></div>
            <el-form-item style="width: 100%" prop="repassword">
              <el-input :clearable="true" :show-password="true" :validate-event="true" type="password" v-model="registerForm.repassword" auto-complete="off" placeholder=""/>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button @click="resetForm"> </el-button>
            <el-button type="primary" @click.native.prevent="register"> </el-button>
          </div>
        </el-dialog>
      </el-form>
    </div>
    </el-col>

<!--    App-->
    <el-col :span="16">
      <div id="appvar" class="grid-content bg-purple">
        <el-table :data="appVerData" height="800px" width="730">
          <el-table-column prop="ID" label="ID" width="350">
          </el-table-column>
          <el-table-column prop="" label="" width="180">
          </el-table-column>
          <el-table-column prop="" label="" width="100">
          </el-table-column>
          <el-table-column prop="" label="">
          </el-table-column>
        </el-table>
      </div>
    </el-col>
  </el-row>
</template>

<script>
  // 
  export function isvalidUser(str) {
    const reg = /[\u4e00-\u9fa5]/;
    return reg.test(str);
  }

  //    8~18
  export function isvalidPass(str) {
    const reg = /^[a-zA-Z]\w{7,18}$/;
    return reg.test(str);
  }

  //   
  export function isvalidUsername(str) {
    const reg = /^(?![^A-Za-z]+$)(?![^0-9]+$)[0-9A-Za-z_]{4,15}$/;
    return reg.test(str);
  }

  var qs = require('qs');

  export default {
    name: 'Login',

    data () {
      //
      let validUser = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else if (!isvalidUser(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      };

      //
      let validUsername = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else if (!isvalidUsername(value)) {
          callback(new Error("  4-15"));
        } else {
          callback();
        }
      };

      //
      let validatePass = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else if (!isvalidPass(value)) {
          callback(new Error(" 8~18 "));
        } else {
          if (this.registerForm.repassword !== "") {
            this.$refs.registerFormRef.validateField("repassword");
          }
          callback();
        }
      };

      //
      let validatePass2 = (rule, value, callback) => {
        if (!value) {
          callback(new Error(""));
        } else if (value !== this.registerForm.password) {
          callback(new Error(""));
        } else {
          callback();
        }
      };

      return {
        login_username: '',
        login_password: '',
        loginForm: {
          username: '',
          password: ''
        },

        loginipData: {
          ip:'',
          city:'',
          device:'',
          os:''
        },

        registerForm: {
          user: '',
          username: '',
          password: '',
          repassword: ''
        },

        //
        loginRules: {
          username: [
            { required: true, message: '', trigger: "blur" }
          ],
          password: [
            { required: true, message: '', trigger: "blur" }
          ]
        },

        //
        registerRules: {
          user: [
            { required: true, validator: validUser, trigger: "blur" }
          ],
          username: [
            { required: true, validator: validUsername, trigger: "blur" }
          ],
          password: [
            { required: true, validator: validatePass, trigger: "blur" }
          ],
          repassword: [
            { required: true, validator: validatePass2, trigger: "blur" }
          ]
        },
        dialogFormVisible: false,
        responseResult: [],
        loading: true,
        changValue: null,
        appVerData: []
      }
    },

    mounted:function () {
      this.getAppVerData();
      this.getLoginipData();
    },

    methods: {
      // 
      login() {
        this.$refs.loginFormRef.validate((valid) => {
          if(valid){
            this.$axios.post('/login', {
              login_username: this.loginForm.username,
              login_password: this.loginForm.password
            })
              .then(res => {
                if (res.status === 200 && res.data && res.data.code === 200) {
                  sessionStorage.setItem("user_id",res.data.result.user_id);
                  sessionStorage.setItem("user_name",res.data.result.user_name);
                  sessionStorage.setItem("login_username",res.data.result.login_username);
                  sessionStorage.setItem("user_sex",res.data.result.user_sex);
                  sessionStorage.setItem("msg","login success!");
                  sessionStorage.setItem("user_level",res.data.result.user_level);
                  this.$message({
                    message: '',
                    type: 'success',
                    center: true
                  });
                  //sessionStorage

                  let redirect = decodeURIComponent('/indextools')
                  this.$router.push({path:redirect})

                  //
                  var params = qs.stringify({
                    ip_city: this.loginipData.city,
                    ip_ip: this.loginipData.ip,
                    ip_device: this.loginipData.device,
                    ip_OS: this.loginipData.os
                  })
                  this.$axios.post('/loginip/insert', params)
                    .then(res1 => {
                      if (res1.status === 200 && res1.data && res1.data.code === 201) {
                        this.$message({
                          message: '',
                          type: 'error',
                          center: true
                        });
                      }
                    });
                } else if (res.status === 200 && res.data && res.data.code === 201) {
                  this.$message({
                    message: '',
                    type: 'error',
                    center: true
                  });
                } else if (res.status === 200 && res.data && res.data.code === 202) {
                  this.$message({
                    message: '',
                    type: 'warning',
                    center: true
                  });
                } else if (res.status === 200 && res.data && res.data.code === 203) {
                  this.$message({
                    message: '!',
                    type: 'warning',
                    center: true
                  });
                } else if (res.status === 200 && res.data && res.data.code === 204) {
                  this.$message({
                    message: '',
                    type: 'warning',
                    center: true
                  });
                } else if (res.status === 200 && res.data && res.data.code === 205) {
                  this.$message({
                    message: '',
                    type: 'warning',
                    center: true
                  });
                } else if (res.status === 200 && res.data && res.data.code === 206) {
                  this.$message({
                    message: '',
                    type: 'warning',
                    center: true
                  });
                }
              })
              .catch(failResponse => {})
          }else {
            return false;
          }
        });
      },

      //
      register() {
        this.$refs.registerFormRef.validate((valid) => {
          if (valid) {
            this.$axios.post('/register', {
              user_name: this.registerForm.user,
              login_username: this.registerForm.username,
              login_password: this.registerForm.password
            })
              .then(res => {
                if (res.status === 200 && res.data && res.data.code === 200) {
                  this.$message({
                    message: '!',
                    type: 'success',
                    center: true
                  });
                  this.$router.go(0);
                } else if(res.status === 200 && res.data && res.data.code === 201) {
                  this.$message({
                    message: '',
                    type: 'error',
                    center: true
                  });
                }else if(res.status === 200 && res.data && res.data.code === 202) {
                  this.$message({
                    message: '',
                    type: 'warning',
                    center: true
                  });
                }
              })
              .catch(err => {
                console.log(err);
              });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },

      //
      resetForm() {
        this.$refs.registerFormRef.resetFields();
      },

      //
      getLoginipData() {
        var that = this;
        this.$axios({
            url:'http://ip-api.com/json/?lang=zh-CN',
            method: 'post',
            withCredentials: false //main.jscookietruecookie
        })
        .then((res) =>{
          that.loginipData.ip = res.data.query
          that.loginipData.city = res.data.country + ' ' + res.data.regionName + res.data.city
          that.loginipData.device = that.$IpUtils.GetCurrentBrowser()
          that.loginipData.os = that.$IpUtils.GetOs()
        })
      },

      //json
      getAppVerData() {
        var getData = this;
        this.$axios.get('/getAppVar')
        .then((res) => {
          if (res.status === 200 && res.data && res.data.code === 200) {
            getData.appVerData = JSON.parse(res.data.result);
          } else if(res.status === 200 && res.data && res.data.code === 201) {
            alert('AppVerData');
          }
        })
        .catch(err =>{
          console.log(err);
        })
      }
    }
  }
</script>

<style>
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 30px auto;
    width: 350px;
    height: 500px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  #appvar {
    margin-top: 50px;
  }
</style>



